<template>
  <v-card style="border-radius: 40px;" color="#eaf1f6" flat :disabled="estaBloqueado && !opcionBloquear">
    <v-card-title v-if="editarProducto" class="pa-2 text-center"
                  style="background-color: gray;display: flex;margin-left: 85%;
                    border-bottom-left-radius: inherit">
      <v-btn icon x-small dark @click="editarProducto()">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
    </v-card-title>
    <v-row>
      <v-col cols="4" class="text-center flex-column pr-0" style="display: flex">
        <div class="py-2 flex-column text-center alineacion">
          <v-img height="100px" width="100px" :src="imgSrc" lazy-src="../../assets/estado_vacio.svg" />
        </div>
      </v-col>
      <v-col cols="8" class="pl-0 ml-0">
        <v-card-text class="pa-0">
          <v-row>
            <v-col cols="12">
              <h3 style="font-family: Nunito;" class="black--text">{{ producto.nombre }}</h3>
              <h4 class="text--disabled" style="font-family: Readex Pro, sans-serif;">{{ producto.descripcion }}</h4>
              <h4 class="text--disabled" style="font-family: Readex Pro, sans-serif;">{{ producto.contenidoNeto }}</h4>
              <h4 class="font-weight-bold precios--text" style="font-family: Readex Pro, sans-serif;">{{ producto.costo | moneda }}</h4>
            </v-col>
          </v-row>
        </v-card-text>
      </v-col>
    </v-row>

    <template v-if="mostrarAcciones">
      <template v-if="estaBloqueado">
        <v-card-actions v-if="!opcionBloquear"
                        class="font-weight-bold white--text"
                        style="display: flex;justify-content: center;align-items: center; background-color: grey">
          Producto Bloqueado
        </v-card-actions>

        <v-card-actions v-else style="display: flex;justify-content: center;align-items: center; background-color: grey">
          <v-btn text small dark @click="$emit('desbloquear-producto')">Desbloquear</v-btn>
        </v-card-actions>
      </template>

      <v-card-actions v-else-if="opcionBloquear"  class="primary" style="display: flex;justify-content: center;align-items: center;">
        <v-btn text small dark @click="$emit('bloquear-producto')">Bloquear</v-btn>
      </v-card-actions>

      <template v-else-if="!productoAgregado">
        <v-card-actions class="primary" style="display: flex;justify-content: center;align-items: center;">
          <v-btn v-if="opcionAgregar" text small dark @click="$emit('seleccionar')">Agregar</v-btn>
        </v-card-actions>
      </template>

      <v-card-actions v-else class="font-weight-bold white--text"
                      style="display: flex;justify-content: center;align-items: center; background-color: #8bc34a">
        <v-btn text small dark>Producto Agregado</v-btn>
      </v-card-actions>

    </template>
  </v-card>
</template>

<script>
export default {
  name: "Producto",
  props: {
    opcionAgregar: { type: Boolean, default: false },
    opcionBloquear: { type: Boolean, default: false },
    producto: {type: Object, required: true},
    productosBloqueadosAlumno: { type: Array, default: () => [] },
    mostrarAcciones: { type: Boolean, default: false },
    productoAgregado: { type: Boolean, default: false },
    editarProducto: { type: Function }
  },
  data(){
    return{
      verDetalles: false
    }
  },
  computed: {
    estaBloqueado() {
      const producto = this.producto
      return this.productosBloqueadosAlumno.some(productoBloqueado => producto.id === productoBloqueado.producto.id)
    },
    imgSrc (){
      return this.$getImgCategoria(this.producto.categoria.id)
    }
  }
}
</script>

<style scoped>
.alineacion{
  justify-content: center;
  align-items: center;
  display: flex;
}
</style>
