class Orden {
  constructor({id = 0, alumno, fecha, ciclo = null, productos = [], cafeteria, clienteInternet, menu}) {
    this.id = id
    this.alumno = alumno || null
    this.idAlumno = alumno ? alumno.id : null
    this.fecha = fecha
    this.ciclo = ciclo
    this.costo = 0
    this.estaEntregada = false
    this.productos = productos
    this.cafeteria = cafeteria || null
    this.idCafeteria = cafeteria ? cafeteria.id : null
    this.clienteInternet = clienteInternet || null
    this.idClienteInternet = clienteInternet ? clienteInternet.id : null
    this.menu = menu || null
    this.idMenu = menu ? menu.id : null
  }
}

export default Orden;
