<template>
  <div>
    <v-card v-if="!$vuetify.breakpoint.smAndDown" color="blue lighten-5" style="font-family: Nunito;" class="text-center" flat rounded>
      <v-card-text>
        <v-row>
          <v-col v-if="alumno" cols="4" @click="dialogSeleccionarAlumno = true">
            <item-opcion :texto="alumno.nombre" :imagen="alumno.refImagen" descripcion="Alumno"/>
          </v-col>
          <v-col cols="4">
            <select-fecha :value="fecha" @input="$emit('cambiar-fecha', $event)" />
          </v-col>
          <v-col cols="4">
            <item-opcion :texto="saldo |moneda" :imagen="require('../../../assets/dinero.png')" descripcion="Saldo disponible"/>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-bottom-navigation v-else grow fixed app color="primary" style="font-family: Nunito"  background-color="blue lighten-5">
      <v-btn v-if="alumno" @click="dialogSeleccionarAlumno = true" >
        <span> {{ alumno.nombre }} </span>
        <img :src="alumno.refImagen" width="30"/>
      </v-btn>

      <select-fecha :value="fecha" @input="$emit('cambiar-fecha', $event)"/>

      <v-btn>
        <span> {{ saldo |moneda }} </span>

        <img :src="require('../../../assets/dinero.png')" width="30"/>
      </v-btn>
    </v-bottom-navigation>
    <dialogo v-model="dialogSeleccionarAlumno" title="">
      <v-card class="text-center" style="font-family: Nunito" flat>
        <v-card-title style="display: grid; text-align: center">
          <h4 class="text--disabled">Seleccione un alumno</h4>
        </v-card-title>
        <v-card-text>
          <div v-for="(hijo, indexHijo) in $usuario.hijos" :key="indexHijo" @click="seleccionarHijo(hijo)">
            <item-opcion :texto="hijo.alumno.nombreCompleto" :imagen="hijo.alumno.refImagen"/>
          </div>
        </v-card-text>
      </v-card>
    </dialogo>
  </div>
</template>

<script>
    import ItemOpcion from "./ItemOpcion";
    import SelectFecha from "../SelectFecha";
    export default {
        name: "PanelOpciones",
      props: {
        alumno: {type: Object, required: false},
        fecha: {type: String, required: false},
        saldo: {type: Number, required: true},
      },
      components: {SelectFecha, ItemOpcion},

      data () {
          return{
            dialogSeleccionarAlumno: false
          }
      },
      methods:{
          seleccionarHijo(hijo){
            this.$emit('seleccionar-hijo', hijo)
            this.dialogSeleccionarAlumno = false
          }
      }
    }
</script>

<style scoped>

</style>
