<template>
    <v-card class="text-center mx-0" :color="estaSeleccionada ? '#2cafca' : ''" :dark="estaSeleccionada"
            :elevation="estaSeleccionada ? '0' : ''" style="border-radius: 10px" @click="$emit('seleccionarCategoria')"
            :to="to ? { name: to } : null">
      <div class="py-2 flex-column text-center alineacion">
        <v-img height="100px" width="100px" :src="imgSrc" lazy-src="../../assets/estado_vacio.svg" />
      </div>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <h2>{{ categoria.titulo }}</h2>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
</template>

<script>
  export default {
    name: "Categoria",
    props: {
      categoria: {type: Object, required: true},
      estaSeleccionada: {type: Boolean, required: false},
      to: {type: String, default: null}
    },
    data () {
      return {
        imgSrc: this.$getImgCategoria(this.categoria.id)
      }
    }
  }
</script>

<style scoped>
  .alineacion{
    justify-content: center;
    align-items: center;
    display: flex;
  }
</style>
