import AuthConst from '../config/AuthConst';
import http from "./http";

const getProductos = () => {
  let url = AuthConst.BASE_URL + '/productos';
  return http.get(url)
};

const getByCliente = (idCliente) => {
  let url = AuthConst.BASE_URL + '/productos/id-cliente/' + idCliente;
  return http.get(url)
};

const create = (producto) => {
  let url = AuthConst.BASE_URL + '/productos';
  return http.post(url, producto)
};

const update = (producto) => {
  let url = AuthConst.BASE_URL + '/productos';
  return http.put(url, producto)
};

const bloquearProducto = (productoBloqueado) => {
  let url = AuthConst.BASE_URL + '/productos/bloqueos';
  return http.post(url, productoBloqueado)
};

const desbloquearProducto = (id) => {
  let url = AuthConst.BASE_URL + '/productos/bloqueos/' + id;
  return http.remove(url)
};

export default { getProductos, getByCliente, create, update, bloquearProducto, desbloquearProducto }
